* {
  box-sizing: border-box;
  overscroll-behavior: none;
  margin: 0;
  padding: 0;
}

:root {
  --bg-color: #121212;
}

body {
  background-color: var(--bg-color);
}

.flex {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container {
  width: 100%;
  height: 800px;
  opacity: 0;
  background-color: #fff;
  flex-direction: column;
  margin-top: 8vh;
  transition: opacity 1s cubic-bezier(.87, .07, .63, .71) 1s;
  position: relative;
  overflow: hidden;
}

.active-overlay {
  visibility: visible;
  opacity: 0;
}

.navigator {
  position: relative;
  bottom: -4vh;
}

.button-group {
  width: 100%;
  opacity: 1;
  margin-top: 10px;
  transition: opacity .4s ease-out;
}

.button-group button {
  width: 100px;
  color: #181818;
  -webkit-user-select: none;
  user-select: none;
  background-color: #0000;
  border: 1px solid #000;
  border-radius: 3px;
  margin: auto .5vw;
  padding: 10px 24px;
  font-family: monospace, sans-serif;
  transition: scale .2s ease-out, color .5s ease-out;
}

.button-group .content {
  background-color: gray;
}

.button-group .device {
  color: #969696;
  background-color: #2d2d2d;
}

button:hover.device {
  color: #d5d5d5;
  scale: 1.06;
}

button:hover.content {
  color: #3a3a3a;
  scale: 1.06;
}

img {
  position: fixed;
}

#toggle-frame {
  margin-top: 2vh;
}

.active:is(.mobile-device, .tablet-device, .laptop-device, .desktop-device) {
  opacity: 1;
  z-index: 3;
}

.mobile {
  opacity: 1;
  z-index: 1;
  transform-origin: 0 0;
  height: 1100px;
  width: 825px;
  background-color: #fff;
  transition: opacity .4s ease-in;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: scale(.7)translate(-50%, -50%);
}

.mobile-device {
  opacity: 0;
  transform-origin: 0 0;
  z-index: 2;
  pointer-events: none;
  transition: opacity .4s ease-in;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(.72)translate(0);
}

.mobile-iframe {
  width: 398px;
