@media only screen and (max-width: 768px) {
  .device {
    display: none;
  }

  .button-group.flex {
    flex-direction: column;
  }

  .mobile {
    background-color: var(--bg-color);
    transition-duration: 0s;
    transform: scale(.95)translate(-50%, -60%);
  }

  .mobile-iframe {
    top: 240px;
  }

  .mobile-device, .overlay, .container {
    transition-duration: 0s;
  }

  .devices > img {
    display: none;
  }

  .button-group {
    margin-top: 0;
  }

  .button-group button {
    margin-top: 2px;
    padding: 5px;
  }
}

@media only screen and (max-height: 1100px) {
  .button-group {
    margin-top: 5px;
  }

  .button-group button {
    width: 90px;
    margin: auto .5vw;
    padding: 5px 12px;
  }

  .navigator {
    bottom: -2vh;
  }

  .container {
    height: 78vh;
    margin-top: 5vh;
  }

  #toggle-frame {
    margin-top: 1.2vh;
  }

  .mobile {
    top: 50%;
    left: 50%;
    transform: scale(.55)translate(-50%, -50%);
  }

  .tablet, .laptop {
    top: 50%;
    left: 50%;
    transform: scale(.65)translate(-50%, -50%);
  }

  .desktop {
    top: 50%;
    left: 50%;
    transform: scale(.55)translate(-50%, -50%);
  }
}

